import React, { useContext } from "react";
import styled from "styled-components";
import Player from "./display/Player";
import media from "../utils/media";
import context from "../contexts";

const Wrapper = styled.div`
  position: relative;
  width: 100%;
  padding-top: 56%;
  border-radius: 0.5rem;
  overflow: hidden;
  display: flex;
  background-color: black;
`;

const Container = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
`;

const Panel = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: black;
  padding: 20px 5px;
`;

const Icon = styled.i`
  color: white;
  font-size: 3vw;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
    transition: all 0.2s linear;
  }

  ${media.mobile`font-size: 1rem;`}
`;

const toggleFullScreen = () => {
  if (document.fullscreen) {
    closeFullscreen();
  } else {
    openFullscreen();
  }
};

function openFullscreen() {
  var elem = document.getElementById("display");
  if (elem.requestFullscreen) {
    elem.requestFullscreen();
  } else if (elem.mozRequestFullScreen) {
    /* Firefox */
    elem.mozRequestFullScreen();
  } else if (elem.webkitRequestFullscreen) {
    /* Chrome, Safari and Opera */
    elem.webkitRequestFullscreen();
  } else if (elem.msRequestFullscreen) {
    /* IE/Edge */
    elem.msRequestFullscreen();
  }
}
function closeFullscreen() {
  if (document.exitFullscreen) {
    document.exitFullscreen();
  } else if (document.mozCancelFullScreen) {
    /* Firefox */
    document.mozCancelFullScreen();
  } else if (document.webkitExitFullscreen) {
    /* Chrome, Safari and Opera */
    document.webkitExitFullscreen();
  } else if (document.msExitFullscreen) {
    /* IE/Edge */
    document.msExitFullscreen();
  }
}

function Display() {
  const { dispatch } = useContext(context);

  const skipNext = () => {
    dispatch.video({ type: "NEXT" });
  };

  const skipPrev = () => {
    dispatch.video({ type: "PREV" });
  };

  return (
    <Wrapper>
      <Container id="display">
        <Player></Player>
        <Panel>
          <Icon className="material-icons" style={{ color: "#f44336" }}>
            favorite
          </Icon>
          <Icon className="material-icons" onClick={toggleFullScreen}>
            fullscreen
          </Icon>
          <Icon className="material-icons" onClick={skipNext}>
            skip_next
          </Icon>
          <Icon className="material-icons" onClick={skipPrev}>
            skip_previous
          </Icon>
        </Panel>
      </Container>
    </Wrapper>
  );
}

export default Display;
